body {
    margin: 0;
    background-color: #222;
}
.title {
    text-align: center;
    font-family: -apple-system, BlinkMacSystemFont, 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
    color: white;
}
label {
    font-family: -apple-system, BlinkMacSystemFont, 'Helvetica Neue',
    sans-serif;
}
.form {
    display: flex;
    justify-content: center;
    margin: 15px 0px 15px 0px;
    align-items: center;
    color: white;
}
.card-list {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
}
.card {
    width: 31%;
    display: flex;
    flex-direction: column;
    align-items: center;
    margin: 0.5rem;
    background: #333;
    color: white;
    border-radius: 10px;
    box-shadow: 0 0 10px black;
}
.stadiumLocation {
    font-style: italic;
}
.teamLogo {
    margin: 10px 0;
    width: 20%;
}
.teamName {
    margin: 0;
    font-size: 23px;
    font-family: -apple-system, BlinkMacSystemFont, 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;
}
.stadiumPic {
    max-width: 100%;
}
.stadiumName {
    font-family: -apple-system, BlinkMacSystemFont, 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;
    margin-bottom: 0 !important;
}
.card p {
    margin: 5px 0;
    text-align: center;
}
.est {
    margin: 8px 0 !important;
}
.socials {
    display: flex;
    max-width: 50%;
    justify-content: center;
}
.socials img {
    max-width: 60%;
    padding: 5px 10px 15px 10px;
}
.socials a {
    max-width: 3rem;
}
.credits {
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    margin-top: 100px;
}
.credits img {
    width: 125px;
}
select {
    -webkit-appearance:none;
    -moz-appearance:none;
    -ms-appearance:none;
    appearance:none;
    outline:none;
    box-shadow:none;
    border:0!important;
    background: #333;
    background-image: none;
    flex: 1;
    padding: 0 .5em;
    color:#fff;
    cursor:pointer;
    font-size: 1em;
    font-family: 'Open Sans', sans-serif;
 }
 select::-ms-expand {
    display: none;
 }
 .select {
    position: relative;
    display: flex;
    width: 20em;
    height: 3em;
    line-height: 3;
    background: #5c6664;
    overflow: hidden;
    border-radius: .25em;
 }
 .select::after {
    content: '\25BC';
    position: absolute;
    top: 0;
    right: 0;
    padding: 0 1em;
    background: #333;
    cursor:pointer;
    pointer-events:none;
    transition:.25s all ease;
    border-left: solid 2px #222;
 }
 .select:hover::after {
    color: #15884d;
 }
 .button {
    height: 3em;
    padding: 0 .5em;
    font-size: 1em;
    color: white;
    background: #333;
    border: none;
    margin: 0 0 0 25px;
    padding: 0 25px;
    border-radius: .25em;
    outline: none;
}
.button:hover {
    background: #15884d;
    transition: .25s all ease;
}
*:focus {
    outline: 0 !important;
}


@media only screen and (max-width: 900px) {
    .card {
      width: 45%;
    }
}